import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { ManagementComponent } from './management.component';

const routes: Routes = [
  {
    path: '',
    component: ManagementComponent
  },
  {
    path: 'dishes',
    loadChildren: () => import('./dishes/dishes.module').then(m => m.DishesModule)
  },
  {
    path: 'design',
    loadChildren: () => import('./design/design.module').then(m => m.DesignModule)
  },
  {
    path: 'service',
    loadChildren: () => import('./service/service.module').then(m => m.ServiceModule)
  },
  {
    path: 'ingredient',
    loadChildren: () => import('./ingredient/ingredient.module').then(m => m.IngredientModule)
  },

  {
    path: 'invoice',
    loadChildren: () => import('./invoice/invoice.module').then(m => m.InvoiceModule)
  },
  {
    path: 'partner',
    loadChildren: () => import('./partner/partner.module').then(m => m.PartnerModule)
  },
  {
    path: 'tax',
    loadChildren: () => import('./tax/tax.module').then(m => m.TaxModule)
  },
  {
    path: 'sales-program',
    loadChildren: () => import('./sales-program/sales-program.module').then(m => m.SalesProgramModule)
  },
  {
    path: 'cashflow',
    loadChildren: () => import('./cashflow/cashflow.module').then(m => m.CashflowModule)
  },
  {
    path: 'report',
    loadChildren: () => import('./reports/reports.module').then(m => m.ReportsModule)
  },
  {
    path: 'stock',
    loadChildren: () => import('../management/stock/stock.module').then(m => m.StockModule)
  },

  {
    path: 'branch',
    loadChildren: () => import('./branch/branch.module').then(m => m.BranchModule)
  },
]

@NgModule({
  declarations: [ManagementComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes)
  ]
})
export class ManagementModule { }
